html {
  box-sizing: border-box;
  height: 100%;

  // font-size: 20px;
  // overflow-anchor: none;
}

body {
  font-family: $font;
  // color: red;
  @include font-body();
}

.text {
  p {
    margin-bottom: 1.2em;

    a {
      color: currentColor;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }

  p.intro {
    @include font-body-m();
    margin-bottom: 1.5em;
  }
}
